import React, { Component, Fragment, Suspense } from 'react';
import { Route, Switch, Redirect } from 'react-router-dom';
import { Col, Row, Nav, NavLink, NavItem, Collapse } from 'reactstrap';
import { codesheet_routes as routes } from '../routes';
import { local_urls } from '../urls';
import Icon from '@mdi/react';
import { mdiMenu } from '@mdi/js';
import { HashLink } from 'react-router-hash-link';

export default class Components extends Component {
    constructor(props) {
        super(props);
        this.state = {
            is_menu_open: true
        };
    }

    showMenu = function () {
        this.setState({ is_menu_open: !this.state.is_menu_open });
    }.bind(this)

    render() {
        var side_menu = <Fragment>
            <NavItem>
                <HashLink className="nav-link" to={`${local_urls["version_prefix"]}/codesheet`}>Types</HashLink>
                <Nav vertical>
                    {routes.map((item, key) => {
                        if (key === 0) {
                            return null;
                        }

                        return <NavItem>
                            <HashLink className="nav-link" to={item.path}>{item.name}</HashLink>
                        </NavItem>
                    })}
                </Nav>
            </NavItem>
            <NavItem className="nav-link-title mt-2">
                Tools
            </NavItem>
            <NavItem>
                <HashLink className="nav-link" to={`${local_urls["version_prefix"]}/playground`}>Playground</HashLink>
            </NavItem>
            <NavItem>
                <HashLink className="nav-link" to={`${local_urls["version_prefix"]}/components/designer/translation-generator`}>Translation generator</HashLink>
            </NavItem>
        </Fragment>;

        return (
            <Fragment>
                <Row className="pt-3" >
                    <Col sm="12" className="mx-300-alter p-0">
                        <Nav vertical>
                            <NavItem className="nav-link-title">
                                Codesheet
                                <button className="navbar-toggler float-right" onClick={this.showMenu}>
                                    <Icon path={mdiMenu} color="#77757a" size={1.1} />
                                </button>
                            </NavItem>
                        </Nav>
                        <Collapse isOpen={this.state.is_menu_open} >
                            <Nav vertical>
                                {side_menu}
                            </Nav>
                        </Collapse>
                    </Col>
                    <Col>
                        <Suspense fallback={this.props.page_loading}>
                            <Switch>
                                {routes.map((route, idx) => {
                                    return route.component ? (
                                        <Route
                                            key={idx}
                                            path={route.path}
                                            exact={route.exact}
                                            name={route.name}
                                            render={props => (
                                                <route.component {...props}
                                                    page_loading={this.props.page_loading}
                                                    loading={this.props.loading}
                                                    processing={this.props.processing}
                                                    onRedirect={this.props.navigate}
                                                />
                                            )} />
                                    ) : (null);
                                })}
                                <Redirect to={`${local_urls["version_prefix"]}/codesheet`} />
                            </Switch>
                        </Suspense>
                    </Col>
                    <Col className="mx-300">
                        <Nav vertical>
                            <NavItem className="nav-link-title">Codesheet</NavItem>
                            {side_menu}
                        </Nav>
                    </Col>
                </Row>
            </Fragment>
        );

    }
}